import _canvas from './uniapp/index'
import easyChart from './easyChart';
class EasyPoster {

    constructor(options) {
        this.ctx = options.ctx;
    }
    /**
     * ç»å¶ä¸ä¸ªç©å½¢
     * @param {number} x         @xè½´
     * @param {number} y         @yè½´  
     * @param {number} width     @å®½åº¦
     * @param {number} height    @é«åº¦
     * @param {number} radius    @åè§å¼§åº¦
     * @param {string} fillColor @èæ¯é¢è² 
     */
    easyRectangle(x, y, width, height, radius, fillColor) {

        const ctx = this.ctx;

        _canvas.fillRoundRect(ctx, x, y, width, height, radius, fillColor);
    }
    /**
     * ç»å¶æå­
     * @param {string} str         @æå­åå®¹
     * @param {number} x           @xè½´  
     * @param {number} y           @yè½´
     * @param {number} fontSize    @æå­å¤§å°
     * @param {string} fontColor   @æå­é¢è²
     * @param {string} family      @å­ä½
     */
    easyText(str, x, y, fontSize, fontColor, family) {
        const ctx = this.ctx
        
        if (!family) {
            family = " 'PingFang SC',tahoma,arial,'helvetica neue','hiragino sans gb','microsoft yahei',sans-serif";
        }else {
            family = `  ${family}`;
        }

        ctx.restore();
        ctx.setFillStyle(fontColor)

        ctx.font = fontSize + 'px' + family
        ctx.fillText(str, x, y)
    }

    /**
     * è·åå¾ç
     * Promise
     * @param {string} image @å¾çå°å
     * @returns 
     */
    easyGetImageInfo(image) {
        return new Promise((req, rj) => {
            // #ifndef APP-PLUS
            uni.getImageInfo({
                src: image,
                success: function (res) {
                    req(res)
                },
                fail: function (err) {
                    rj(err)
                }
            });
            // #endif
            // #ifdef APP-PLUS
            if (uni.getSystemInfoSync().platform == 'ios') {
                uni.downloadFile({
                    url: image,
                    success: (res) => {
                        res.path = res.tempFilePath
                        req(res)
                    },
                    fail: (err) => {
                        rj(err)
                    }
                })
            } else {
                uni.getImageInfo({
                    src: image,
                    success: function (res) {
                        req(res)
                    },
                    fail: function (err) {
                        rj(err)
                    }
                });
            }
            // #endif
        })
    }

    /**
     * ç»å¶å¾çcover
     * @param {Object} path   @å¾çå±æ§(ç½ç»å¾ä¸å¯ç¨éè¦ç¨easyGetImageInfoè·åç¼å­å¾ä¹åä½¿ç¨)
     * @param {number} x      @xè½´
     * @param {number} y      @yè½´
     * @param {number} width  @ç»å¶çå¾çå®½åº¦
     * @param {number} height @ç»å¶çå¾çé«åº¦ 
     */
    easyDrawImgCover(path, x, y, width, height) {
        const ctx = this.ctx;

        ctx.save();
        _canvas.drawImgCover(ctx, path, x, y, width, height)

    }

    /**
     * ç»å¶åå½¢å¤´å
     * @param {string} url      @å¾çå°å
     * @param {number} x        @xè½´
     * @param {number} y        @yè½´
     * @param {number} width    @å¾çå®½åº¦
     * @param {number} height   @å¾çé«åº¦
     */
    easyDrawCircular(url, x, y, width, height) {

        const ctx = this.ctx;

        if (url.path) {
            url = url.path
        }
        _canvas.drawCircular(ctx, url, x, y, width, height);
    }
    /**
     * ç»å¶canvaså¹¶ä¸çæä¸ä¸ªå¾çè¿å
     * åæ°åèuniapp api https://uniapp.dcloud.io/api/canvas/canvasToTempFilePath.html
     * @param {object} options 
     * @returns 
     */
    easyDrawSaveImg(options) {

        return new Promise((resolve, reject) => {
            const ctx = this.ctx;

            ctx.draw(true, (res) => {
                uni.canvasToTempFilePath({

                    ...options,
                    success(res) {
                        resolve(res.tempFilePath)
                    },
                    fail(err) {
                        reject(err)
                    }
                })
            })
        })

    }
    /**
     * ç»å¶å¾è¡¨æ¹æ³
     * @param {Object} uCharts @uChartså¯¹è±¡ https://www.ucharts.cn/
     * @param {String} uChartsCanvasId @uChartsçcanvasId
     * @param {Object} uChartsConfig @uChartsçå¾è¡¨éç½®é¡¹
     * @param {Object} easyConfig  @éç½®æä»¶
     * {
     *  x: 10,  xè½´
     *  y: 10,  yè½´
     *  width: 300, å®½åº¦
     *  height: 181 é«åº¦
     * }
     * @returns 
     */
    async easyDrawChart(uCharts, uChartsCanvasId, uChartsConfig, easyConfig) {
        let defaultOptions = {
            x: 0,
            y: 0,
            width: 300,
            height: 300
        }
        let options = easyConfig ? Object.assign(defaultOptions, easyConfig) : defaultOptions;

        return new Promise(async (resolve, reject) => {

            const lc = new easyChart({
                canvasId:uChartsCanvasId,
                uCharts,
                uChartsConfig,
                config: {
                    width: options.width,
                    height: options.height
                }
            })
            const initRes = await lc.init()

            if (!initRes) {
                reject(false)
            }

            const drawImg = await this.easyDrawSaveImg({
                canvasId: uChartsCanvasId,
                destWidth: options.width,
                destHeight: options.height,
            })

            const imgInfo = await this.easyGetImageInfo(drawImg);

            this.easyDrawImgCover(imgInfo, options.x, options.y, options.width, options.height);

            resolve(true)
        })




    }
}


export default EasyPoster;